import ProjectCard from '@/components/ProjectCard.vue';

interface Project {
  name: string;
  url: string;
  image: string;
  backgroundColor: string;
}

@Component({
  components: {
    ProjectCard
  },
  computed: mapState([
    'portfolio',
    'projects'
  ])
})
export default class Dashboard extends Vue {
  public portfolio!: string;
  public projects!: Project[];
}
