















import { Component, Prop, Vue } from 'vue-property-decorator'
import { mapState } from 'vuex'

interface Terms {
  name: string;
  website: string;
}

interface Privacy {
  name: string;
  address: string;
  contact: string;
}

@Component({
  computed: mapState([
    'terms',
    'privacy'
  ])
})
export default class Footer extends Vue {
  public terms!: Terms;
  public privacy!: Privacy;
}
